/*-------- Banner --------*/

.banner { 
    position: relative;
    overflow: hidden;
    z-index: 3;
}

.banner {

    &--main:before {
        content: '';
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.5);
        position: absolute;
        left: 0;
        top: 0;
        z-index: 1;
    }

    &--main &__wrapper {
        padding-top: 129px;
        padding-bottom: 202px;
        position: relative;
        z-index: 2;
    }

    &--main &__title {
        margin-top: 85px;

        font-family: $font-pf;
        font-size: 124px;
        color: #ffffff;
    }

    &--main &__title-text-mask {
        overflow: hidden;
    }

    &--main &__title-text--blue {
        color: $blue;
    }

    &--main &__text {
        display: flex;
        flex-wrap: wrap;
        margin-top: 34px;

        max-width: 671px;
        font-family: $font-r;
        font-size: 30px;
        color: #ffffff;
        line-height: 1.4;

        overflow: hidden;
        white-space: pre;
    }

    &--main &__buttons {
        display: flex;
        margin-top: 80px;
    }

    &--main &__button:not(:first-child) {
        margin-left: 18px;
    }
}

.banner {

    &--v2 {
        height: 733px;
        position: relative;
        overflow: hidden;
    }

    &--v2 &__wrapper {
        height: 100%;
        width: 100%;

        position: absolute;
        left: 0;
        top: 0;
    }

    &--v2 &__image {
        width: 100%;
        height: 100%;

        position: absolute;
        left: 0;
        top: 0;
        z-index: 0;

        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
    }

    &--v2 &__image:before {
        content: '';
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        background-color: rgba(0, 0, 0, 0.3);
    }

    &--v2 &__wrapper-inner {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100%;

        z-index: 1;
        position: relative;
    }

    &--v2 &__title {
        font-family: $font-pf;
        font-size: 64px;
        color: #ffffff;
    }

    &--v2 &__arrow {
        width: 16px;
        height: 16px;

        position: absolute;
        bottom: 106px;

        border: 2px solid #ffffff;
        border-left: none;
        border-top: none;
        transform: rotateZ(45deg);
        cursor: pointer;
    }
}

.banner {

    &--case-study &__title {
        font-size: 58px;
    }
}

@media (max-width: 1499px) {

    .banner {

        &--main &__wrapper {
            padding-bottom: 150px;
        }

        &--main &__title {
            margin-top: 60px;
            font-size: 110px;
        }
    }
}

@media (max-width: 1399px) {

    .banner {

        &--main &__title {
            font-size: 80px;
        }

        &--main &__text {
            margin-top: 20px;
            font-size: 25px;
        }

        &--main &__buttons {
            margin-top: 60px;
        }
    }
}

@media (max-width: 1219px) {

    .banner {

        &--main &__title {
            font-size: 64px;
        }

        &--main &__text {
            font-size: 22px;
        }
    }
}

@media (max-width: 1099px) {

    .banner {

        &--main &__wrapper {
            padding-top: 110px;
        }
    }

    .banner {

        &--v2 {
            height: calc(100vh - 90px);
        }

        &--v2 &__title {
            font-size: 52px;
        }

        &--v2 &__arrow {
           bottom: 50px;
        }
    }
}

@media (max-width: 989px) {

    .banner {

        &--main {
            height: 100vh;
            display: flex;
            align-items: center;
        }

        &--main &__wrapper {
            padding-top: 90px;
        }

        &--main &__title {
            margin-top: 40px;
            font-size: 50px;
        }

        &--main &__text {
            margin-top: 15px;
            font-size: 18px;
        }

        &--main &__buttons {
            margin-top: 30px;
        }
    }

    .banner {

        &--v2 &__title {
            font-size: 45px;
        }
    }
}

@media (max-width: 767px) {

    .banner {

        &--v2 {
            height: 300px;
        }
    }
}

@media (max-width: 619px) {

    .banner {

        &--main &__wrapper {
            padding-bottom: 100px;
        }
    }

    .banner {

        &--v2 {
            height: 250px;
        }

        &--v2 &__title {
            font-size: 30px;
        }

        &--v2 &__arrow {
            width: 10px;
            height: 10px;
        }
    }
}

@media (max-width: 479px) {

    .banner {

        &--main &__wrapper {
            padding-bottom: 60px;
        }

        &--main &__title {
            margin-top: 10px;
            font-size: 43px;
        }

        &--main &__text {
            font-size: 15px;
        }
    }
}