/*-------- Sections --------*/

.sections {
    display: flex;
    height: 100vh;

    &__pagination {
        width: 407px;
        padding-top: 151px;
        padding-left: 83px;
        padding-right: 83px;
        background-color: #1d4693;
    }

    &__pagination-title {
        font-family: $font-sb;
        color: #ffffff;
        font-size: 27px;
    }

    &__pagination-list {
        margin-top: 110px;
    }
 
    &__pagination-item {
        display: block;
        padding-bottom: 11px;
        border-bottom: 2px solid #d5d5d55d;

        width: auto;
        height: auto;

        color: #8aa0ca;
        font-family: $font-sb;
        font-size: 19px;

        background-color: unset;
        border-radius: unset;
        opacity: 1;

        transition: color 0.2s ease-out, border-color 0.2s ease-out;
    }

    &__pagination-item.swiper-pagination-bullet-active, &__pagination-item:hover {
        color: #ffffff;
        border-color: #ffffff;
    }

    &__pagination-item:not(:first-child) {
        margin-top: 11px;
    }

    &__pagination-button {
        font-size: inherit;
        color: inherit;
    }

    &__swiper {
        width: calc(100% - 407px);   
        background-color: #f7f7f7;
    }

    &__swiper-container {
        height: 100%; 
    }

    &__swiper-slide {
        padding: 90px 457px 126px 47px;
    }

    &__swiper-title {
        display: flex;
        align-items: center;
        font-family: $font-pf;
        font-size: 58px;
    }

    &__swiper-icon {
        width: 101px;
        margin-right: 34px;
    }

    &__swiper-text {
        margin-top: 52px;
        color: #161616;
        font-size: 19px;
        line-height: 1.5;
    }

    &__swiper-image {
        margin-top: 63px;
        max-width: 1000px;
        max-height: calc(100vh - 481px);
    }
}

.sections {

    &--case-study {
        margin-top: 143px;
    }
}