/*-------- Case-study --------*/

.case-study {

    &__wrapper {
        position: relative; 
        padding-top: 162px;
    }

    &__columns {
        display: flex;   

        z-index: 1;
        position: relative;
    }

    &__column--1 {
        width: calc(100% - 267px);
        padding-right: 320px;
    }

    &__column--2 {
        width: 267px;
    }

    &__title {
        font-family: $font-pf;
        font-size: 58px;
    }

    &__title--small {
        line-height: 2;
        font-family: $font-sb;
        font-size: 27px;
    }

    &__title--columns {
        margin-bottom: 60px;
    }

    &__text {
        color: #161616;
        font-size: 19px;
        line-height: 1.5;
    }

    &__work-item {
        color: #161616;
        font-size: 19px;
    }

    &__work-item:not(:first-child) {
        margin-top: 11px;
        padding-top: 11px;
        border-top: 2px solid #d5d5d5;
    }
}